// slick 套件用元件樣式
// --------------------------------------------------------------- //
.slick-slider {
  position: relative;

  button {
    border: none;outline: none;box-shadow: none;
    padding: 0; margin: 0;width: auto;
  }

  // 點點或數字
  .slick-dots { 
    --tinge: #e0dedb;
    --dotSize: 12px;
    --tingeActive: #{$primaryColor};
    list-style: none; margin:0 auto;padding: 0;
    font-size: 0; text-align: center;
    li { 
      display: inline-block; 
      margin: 0 3px; padding: 2px;
      button, span { 
        width: var(--dotSize); height: var(--dotSize); border-radius: 50%;
        overflow: hidden; box-shadow: none;
        background: var(--tinge);
        &:hover, &:focus{--tinge: #{$primaryColor};}
      }

      &.slick-active{
        --tinge: #{$primaryColor};

        button, span{ 
          box-shadow:var(--tinge) 0 0 0 2px;
          &:focus{ box-shadow:$white 0 0 0 1px, $primaryColor 0 0 0 3px;}
        }
      }

    }
  }

  // 左右箭頭
  button.slick-arrow {
    --buttonSize: 44px;
    --bgc:  #{$white};
    --tinge: #{$black};
 
    position: absolute; z-index: 9; 
    text-indent: -20em; overflow: hidden;font-size: 0;
    height: var(--buttonSize);width: var(--buttonSize); 
    border-radius: 50%;
    background-color: var(--bgc);
    box-shadow: var(--tinge) 0 0 0 1px;
    transition: .2s;

    &::before {
      content: ''; position: absolute;
    }
    &::before {
      top: calc(50% - 6px);
      width: 12px; height: 12px;
      border: 1px solid var(--tinge);
      rotate: 45deg;
    }
    &.slick-prev {
      &::before{
        border-right: none; border-top: none;
        left: calc(50% - 3px);
      }
    }
    &.slick-next {
      &::before{
        border-left: none; border-bottom: none;
        right: calc(50% - 3px);
      }
    }

    &:hover, &:focus{
      --bgc: #{$primaryColor};
      --tinge: #{$white};

      box-shadow: var(--bgc) 0 0 0 2px;
    }
    &:focus{
      box-shadow: var(--tinge) 0 0 0 1px, var(--bgc) 0 0 0 4px;
    }
  }
}
// --------------------------------------------------------------- //


// 自訂的 slick arrows
.slickArrows {
  button {
    --buttonSize: 44px;
    --bgc:  #{$white};
    --tinge: #{$black};
 
    position: absolute; z-index: 9; 
    text-indent: -20em; overflow: hidden;font-size: 0;
    height: var(--buttonSize);width: var(--buttonSize); 
    border-radius: 50%;
    background-color: var(--bgc);
    box-shadow: var(--tinge) 0 0 0 1px;
    border: none;
    transition: .2s;

    &::before {
      content: ''; position: absolute;
    }
    &::before {
      top: calc(50% - 6px);
      width: 12px; height: 12px;
      border: 1px solid var(--tinge);
      rotate: 45deg;
    }
    &.slick-prev {
      &::before{
        border-right: none; border-top: none;
        left: calc(50% - 3px);
      }
    }
    &.slick-next {
      &::before{
        border-left: none; border-bottom: none;
        right: calc(50% - 3px);
      }
    }

    &:hover, &:focus{
      --bgc: #{$primaryColor};
      --tinge: #{$white};

      box-shadow: var(--bgc) 0 0 0 2px;
    }
    &:focus{
      box-shadow: var(--tinge) 0 0 0 1px, var(--bgc) 0 0 0 4px;
    }
  }

}



// 控制輪播暫停、繼續 button
// --------------------------------------------------------------- //
button.pausePlay {
  --size: 44px;
  --tinge: #{$black};
  --bgc: #{$white};
  --borderColor: #{$black};
  --borderWidth: 1px;

  position: absolute;z-index: 19;
  width: var(--size); height: var(--size); border-radius: 50%;
  padding: 0; margin: 5;
  border: none;outline: none;box-shadow: none;
  background-color: var(--bgc);
  box-shadow: var(--borderColor) 0 0 0 var(--borderWidth);
  cursor: pointer;
  transition: .2s;
  &::before, &::after {content: ''; position: absolute;}
  &::before {
    top: calc(50% - 7px); left: calc(50% - 5px);
    width: 2px; height: 14px;
    background-color: var(--tinge);
    box-shadow:  var(--tinge) 8px 0 0 0;
  }
  &::after {
    display: none;
    top: calc(50% - 8px); left: calc(50% - 3px);
    width: 1px; height: 1px;
    border-style: solid;
    border-width: 8px 0 8px 10px;
    border-color: transparent transparent transparent var(--tinge);
  }

  &:hover, &:focus{ 
    --borderWidth: 3px;
  }

  &.paused{
    --bgc: #{$primaryColor};
    --tinge: #{$white};
    --borderColor: #{$primaryColor};

    &:focus {
      box-shadow: var(--tinge) 0 0 0 1px, var(--bgc) 0 0 0 4px;
    }

    &::before{ display: none; }
    &::after{ display: block; }
  }


}
